import React, { Component } from "react";
import { Dropdown } from "react-bootstrap";
//需要参数 page pageSize total onPageChange onPageSizeChange
export default class Pagination extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  componentDidMount() {}

  render() {
    const { page, pageSize, total } = this.props;
    let items = [];
    for (let number = 1; number <= total; number++) {
      items.push(
        <span
          key={number}
          className={number === page ? "active" : ""}
          onClick={() => {
            this.props.onPageChange(number);
          }}
        >
          {number}
        </span>
      );
    }
    return (
      <div className="pagination">
        <span
          className={page === 1 ? "disable" : ""}
          onClick={() => {
            if (page === 1) return;
            this.props.onPageChange(page - 1);
          }}
        >
          上一页
        </span>
        {items}
        <span
          className={page === total ? "disable" : ""}
          onClick={() => {
            if (page === total) return;
            this.props.onPageChange(page + 1);
          }}
        >
          下一页
        </span>
        <Dropdown>
          <Dropdown.Toggle variant="success">{pageSize}</Dropdown.Toggle>

          <Dropdown.Menu>
            <Dropdown.Item
              onClick={() => {
                this.props.onPageSizeChange(5);
              }}
            >
              5
            </Dropdown.Item>

            <Dropdown.Item
              onClick={() => {
                this.props.onPageSizeChange(10);
              }}
            >
              10
            </Dropdown.Item>
            <Dropdown.Item
              onClick={() => {
                this.props.onPageSizeChange(15);
              }}
            >
              15
            </Dropdown.Item>
            <Dropdown.Item
              onClick={() => {
                this.props.onPageSizeChange(20);
              }}
            >
              20
            </Dropdown.Item>
          </Dropdown.Menu>
        </Dropdown>
        /页
      </div>
    );
  }
}
